.movie {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  /* justify-content: space-between; */
}

.movie:not(:last-child) {
  margin-bottom: 10px;
}

.movie-info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.movie-info > *:first-child {
  margin-right: 10px;
}

.title-and-overview {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin-right: 10px;
}

@media screen and (max-width: 700px) {
  .title-and-overview > p:last-of-type {
    display: none;
  }
}

@media screen and (min-width: 700px) {
  .title-and-overview > p:first-of-type {
    margin-bottom: 5px;
  }
}

.title-and-overview > p:first-of-type {
  font-weight: bold;
}

.title-and-overview > p:last-of-type {
  -ms-flex-positive: 1;
      flex-grow: 1;
  font-size: 0.8em;
}

.movie-info .release-date {
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.no-image-movie {
  background-image: url(data:image/svg+xml;utf8;base64,);
}

.toggle-not-watched {
  margin-left: 20px;

  background-image: url(data:image/svg+xml;utf8;base64,);

  cursor: pointer;
}

.toggle-watched {
  margin-left: 20px;

  background-image: url(data:image/svg+xml;utf8;base64,);

  cursor: pointer;
}

.waiting {
  cursor: wait;
}



