.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.container > *:not(:last-child) {
  margin-bottom: 20px;
}

.container-results {
  width: 75%;
  margin-top: 20px;
  padding: 10px;
  border: 2px solid blue;
  border-radius: 4px;
}

.thin-border {
  border: 1px solid blue;
  border-radius: 4px;
}

button {
  font-weight: bold;
  padding: 7px;
  border: 1px solid blue;
  border-radius: 25%;
  color: blue;
  cursor: pointer;
}

button:not(.no-hover):hover {
  background-color: blue;
  color: white;
}

a {
  color: white;
  text-decoration: none;
}

a:hover {
  color: wheat;
}

.icon {
  -ms-flex-preferred-size: 45px;
      flex-basis: 45px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.background-icon {
  height: 68px;
  background-repeat: no-repeat;
  background-size: 45px 68px;
}



