.directors {
  width: 100%;
}

.directors > li:not(:last-child) {
  margin-bottom: 10px;
}

li.director {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

li.director > *:not(:last-child) {
  margin-right: 10px;
}

.director-info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.director-info > * {
  cursor: pointer;
}

.director-info p.director-name {
  font-size: 2em;
}

.director-info > img,
.director-info > div {
  margin-right: 20px;
}

.no-image-director {
  background-image: url(data:image/svg+xml;utf8;base64,);
}

.add-to-favorites {
  background-image: url(data:image/svg+xml;utf8;base64,);

  cursor: pointer;
}

.remove-from-favorites {
  height: 68px;
  min-width: 45px;
  background-image: url(data:image/svg+xml;utf8;base64,);

  cursor: pointer;
}

.movies-to-watch {
  text-align: center;
  background-image: url(data:image/svg+xml;utf8;base64,);
}

@media screen and (min-width: 700px) {
  .add-to-favorites {
    margin-right: 45px;
  }
  .remove-from-favorites {
    margin-right: 45px;
  }
  li.director .movies-to-watch {
    margin-right: 45px;
  }
}

.waiting {
  cursor: wait;
}



